import axios from './axios';

// roleId
export const getDictionaryByType = () => {
  return axios({
    method: 'get',
    url: '/authcenter/role/list',
    params: {
      appId: 2,
    },
  });
};

// 
export const getUserPrivilege = (groupAppId) => {
  return axios({
    method: 'get',
    // url: '/authcenter/ugroup/users',
    url: '/authcenter/ugroup/role',
    headers: {
      groupAppId,
    },
  });
};

// roleId
export const getUsersInRoom = (groupAppId) => {
  return axios({
    method: 'get',
    // url: '/authcenter/ugroup/users',
    url: '/authcenter/ugroup/users',
    headers: {
      groupAppId,
    },
  });
};

// 
export const changeUserPrivilege = (groupAppId, roleId, userId) => {
  return axios({
    method: 'post',
    // url: '/authcenter/ugroup/users',
    url: '/authcenter/role/change',
    headers: {
      groupAppId,
    },
    data: {
      roleId,
      userId,
    },
  });
};

// 
export const addMembersInRooms = (groupAppId, data) => {
  return axios({
    method: 'post',
    // url: '/authcenter/ugroup/users',
    url: '/authcenter/ugroup/add/default/mutiple/workspace',
    headers: {
      groupAppId,
      targetIds: JSON.stringify(data),
    },
  });
};

// 
export const deleteMembersInRooms = (groupAppId, userId) => {
  return axios({
    method: 'post',
    // url: '/authcenter/ugroup/users',
    url: '/authcenter/ugroup/remove',
    headers: {
      groupAppId,
      targetIds: JSON.stringify([userId]),
    },
  });
};
