

































import { defineComponent, ref } from '@vue/composition-api';
// import axios from '../../../../api/axios';
import axios from '@/api/axios';

/**
 * 
 * @param {Object} data { shareId: string, tags: string[], type: string, relateType: string, }
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6116
 */
export const apiAddTags = (data: any) => {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/addTags',
    data,
  });
};

/**
 * 
 * @param {Array} tagIds { string[] }
 * @see http://yapi.4dshoetech.local/project/115/interface/api/6128
 */
export const apiRemoveTags = (tagIds: string[]) => {
  return axios({
    method: 'post',
    url: '/bff-center/showCommon/deleteTag',
    data: tagIds,
  });
};

interface TagItem {
  id: string,
  tag: string,
}

// 
// tags, shareId, type, relateType
export default defineComponent({
  name: 'TagsEditor',
  props: {
    // 
    tags: {
      type: Array as () => TagItem[],
      default: () => [],
    },
    // ID
    shareId: {
      type: String,
      required: true,
    },
    //   0:;1:;2;3:;4:
    type: {
      type: Number,
      required: true,
    },
    // 0 1 2 3 4
    relateType: {
      type: Number,
      required: true,
    },

    // 
    editable: {
      type: Boolean,
      default: true,
    },
    // 
    maxlength: {
      type: Number,
      default: 30,
    },
    // 
    maxTagsLen: {
      type: Number,
      default: 10,
    },
  },
  setup(props, { root: { $nextTick, $errHandler } }) {
    const inputValue = ref('');
    const inputVisible = ref(false);
    const saveTagInput = ref(null);

    // 
    async function showInput() {
      inputVisible.value = true;
      await $nextTick();
      (saveTagInput.value as any).$refs.input.focus();
    }

    // 
    async function handleInputConfirm() {
      if (inputValue.value.trim()) {
        const { data } = await apiAddTags({
          shareId: props.shareId,
          type: props.type,
          tags: [inputValue.value],
          relateType: props.relateType,
        });
        props.tags.push(...data as TagItem[]);
      }
      inputVisible.value = false;
      inputValue.value = '';
    }

    // tag
    function handleClose(id: string) {
      const i = ((props.tags) as TagItem[]).findIndex((tag: TagItem) => tag.id === id);
      if (i !== -1) {
        props.tags.splice(i, 1);
        try {
          apiRemoveTags([id]);
        } catch (err) {
          $errHandler(err);
        }
      }
    }

    return {
      inputValue,
      inputVisible,
      saveTagInput,
      showInput,
      handleInputConfirm,
      handleClose,
    };
  },
});
