/**
 * my-uploadId
 * @returns {Promise}
 */
export const getMyUploadIds = () => {
  return axios({
    method: 'get',
    url: '/catalogue/yunpanCatalog/getPersonalUploadCIds',
  });
};

export const getCatalogUrl = (catalogId) => {
  return axios.get('/catalogue/yunpanCatalog/getCatalogUrl', {
    params: {
      catalogId,
    },
  });
};

